@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Indie+Flower&family=Sniglet&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Indie+Flower&family=Sniglet&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Della+Respira&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bodyLogin {
  background: url(/tarot-vision/static/media/login.453aae10.png)
  no-repeat;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
}

main h1 {
  text-align: center;
  margin-top: 2%;
  text-shadow: 0px 0px 25px white;
  font-size: 5em;
  color: black;
  font-family: 'Dancing Script', cursive;
}

main form {
  margin-top: 4%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15%;
}

main form input {
  width: 25%;
  text-align: center;
  background: rgba(39, 35, 35, 0.3);
  color: #6b6470;
  font-style: unset;
  font-size: 2em;
  font-weight: 100;
  letter-spacing: 2px;
  box-shadow:  0px 0px 10px white;
  border-radius: 9px;
  font-family: 'Sniglet', cursive;
}

main form button {
  margin-top: 40%;
  margin-left: -50%;
  width: 200%;
  font-size: 1.5em;
  font-weight: 100;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: #281646;
  color: #6b6470;
  box-shadow:  0px 0px 10px white;
  font-family: 'Sniglet', cursive;
}

.logoLogin {
  display: flex;
  justify-content: center;
}

.div-footer {
  text-align: center;
  color: #faeff1;
  clear: both;
  font-family: 'Roboto', 'sans-serif';
}

/* @media only screen (min-width: 375px) and (max-width: 812) {

} */
.bodyMain {
  background-image: url(/tarot-vision/static/media/purpleuniverse.4718e845.jpg);
  background-repeat: no-repeat;
  /* background-size: 200%;
  position: fixed; */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.text-answer {
  color: white;
  font-family: 'Della Respira', serif;
  font-size: 2em;
  line-height: 150%;
}

.div-image {
  margin-top: 5%;
}

.h2answer {
  text-shadow: 0px 0px 25px white;
}

.question {
  margin-top: 25%;
  text-align: center;
  color: white;
  font-size: 4.5em;
  font-family: 'Dancing Script', cursive;
  text-shadow: 0px 0px 25px white;
}

.div-answer {
  text-align: center;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 6%;
}

.button {
  margin-top: 10%;
  margin-bottom: 15%;
  width: 45%;
  font-size: 2em;
  font-weight: 100;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: #281646;
  color: white;
  box-shadow:  0px 0px 10px rgb(119, 119, 119);
  font-family: 'Sniglet', cursive;
}

.div-footer {
  margin-top: 10%;
  text-align: center;
  color: #faeff1;
  font-family: 'Roboto', 'sans-serif';
}

